



























import { Component, Vue, Prop, Emit, Watch } from 'vue-property-decorator'
import { State, Mutation } from 'vuex-class'
import BScroll from 'better-scroll'

@Component({})

export default class cityList extends Vue {
  @Mutation changeTips: any
  @Prop({}) provinceList!:any // 
  @Prop({}) iscitydialog!:any //
  // provinceList:Array<any> = [] // 
  cityList:Array<any> = [] // 
  cityShow:boolean = false // 
  province:string = '' // code
  provinceName:string ='' // name
  scrollY:number = 0 // foodsY
  scroll:any = ''
  cityScroll:any = ''
  @Mutation changeCityData: any
  active:number = 0
  activeCity:number=0
  show:boolean = false
  dataLoading:boolean = true
  /* @State('provinceList') provinceList: any
  @Mutation provinceListFun: any */
  activeItem:any=''

  @Emit('chooseCity') send (item:any) {} // 
  created () {
    // 
    // console.log(localStorage.getItem('provinceList'));
    /* (this as any).$post('/wx/User/get_province_list').then((res:any) => {
      this.provinceList = res.data;
       this.$nextTick(() => {
          this._initScroll()
          this.selectProvince(this.provinceList[0], 0)
        })
      // localStorage.setItem('provinceList', 'aaa')
      // (this as any).provinceListFun(res.data)
    }) */
  }

  mounted () {
    if (this.provinceList.length) {
      this.$nextTick(() => {
        this._initScroll()
        this.selectProvince(this.provinceList[0], 0)
      })
    }
  }

  cancel () {
    this.send({})
  }
  confirm () {
    if (!this.activeItem.city_code) {
      this.changeTips({ show: true, title: '' })
      return
    }
    this.send(this.activeItem)
  }
  selectProvince (item:any, index:number):void { // 
    this.dataLoading = true
    this.province = item.city_code // code
    this.provinceName = item.city_name // name
    this.active = index;
    (this as any).$post('/wx/User/get_city_list', { city_code: this.province }).then((res:any) => {
      if (res.status) {
        this.cityList = res.data
        this.dataLoading = false
        this.activeCity = -1
        this.$nextTick(() => {
          this.cityScroll = new BScroll(this.$refs.city, { click: true })
        })
      }
    })
  }
  _initScroll () { // 
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.province, { click: true })
    })
  }
  returnStep ():void { // 
    this.cityShow = false
  }
  selectCity (item:any, i:any):void {
    item.proCode = this.province // code
    item.proName = this.provinceName
    item.iscitydialog = false
    this.activeCity = i
    // let val:string = ''
    this.activeItem = item
    // this.send(item)
    // this.$router.go(-1); // index
    // (this as any).changeCityData(item)
  }
}
